@use "global" as *;

.contact-link{
    margin-top: rem(40);

    @include mq("md") {
        margin-top: 0;
    }
}

.contact-link__text{
    color: $green;
    font-size: rem(14);
    line-height: 1.285;
    text-align: center;
    margin-top: rem(8);
}

.contact-link__link{
    margin-top: rem(40);
    text-align: center;
}