@use "global" as *;

.policy-terms__inner{

    @include mq("md") {
        max-width: rem(800);
        margin-left: auto;
        margin-right: auto;
    }
}

.policy-terms__title{
    font-size: rem(24);
    font-weight: $bold;

    @include mq("md") {
        font-size: rem(32);
    }
}

.policy-terms__body{
    line-height: 2;
    margin-top: rem(40);

    p:first-of-type + ol{
        margin-top: rem(32);
    }

    p:not(:first-of-type){
        margin-top: rem(32);
    }

    ol{
        padding-left: 1.4em;
        list-style: auto;
        list-style-type: decimal;

        ul{
            margin-left: -1.3em;
            margin-top: 0;
        }

        & + ol{
            margin-top: rem(32);
        }
    }

    ul{
        margin-top: rem(32);
    }
}

