  background-color: #408F95;
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  padding-top: 5.75rem;
  padding-bottom: 3.125rem;
  overflow-y: scroll;
  z-index: -1;
}

.inner {
  width: 100%;
  max-width: 37.5rem;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1130px;
    padding-right: 25px;
    padding-left: 25px;
  }
}

@media screen and (min-width: 768px) {
  .inner--narrow {
    max-width: 46.875rem;
  }
}

@media screen and (min-width: 768px) {
  .inner--policy-terms {
    max-width: 53.125rem;
  }
}

.is-fixed {
  overflow: hidden;
  height: 100%;
}

.js-scroll {
  position: relative;
}

.js-scroll div.color {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 2;
  background-color: #408F95;
}

.link-button {
  display: inline-block;
  background-color: #408F95;
  border: 0.0625rem solid #408F95;
  color: #fff;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  height: 3.375rem;
  line-height: 3.375rem;
  width: 12.625rem;
  margin-bottom: 0.25rem;
  margin-right: 0.25rem;
  position: relative;
  transition: 0.3s;
  z-index: 1;
}
.link-button::before {
  content: "";
  display: inline-block;
  border: 0.0625rem solid #408F95;
  height: 3.375rem;
  width: 12.625rem;
  position: absolute;
  top: 0.1875rem;
  right: -0.3125rem;
  z-index: -1;
}
.link-button::after {
  content: "";
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.3s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}
.link-button:hover::after, .link-button:focus::after {
  transform: scale(1, 1);
}
.link-button:hover, .link-button:focus {
  color: #408F95;
  transition: 0.3s;
}

.link-button--404 {
  border: 0.0625rem solid #fff;
  background-color: #fff;
  color: #408F95;
}
.link-button--404::before {
  border: 0.0625rem solid #fff;
}
.link-button--404::after {
  background-color: #408F95;
}
.link-button--404:hover, .link-button--404:focus {
  color: #fff;
}

.link-button__text--send {
  color: #fff;
  text-transform: capitalize;
  text-align: left;
  width: 100%;
  padding-left: 3.375rem;
}

.link-button__arrow {
  display: inline-block;
  margin-left: 0.375rem;
  position: relative;
  height: 0.5rem;
  width: 2.625rem;
}
.link-button__arrow::before {
  content: "";
  display: inline-block;
  background-color: #fff;
  mask-image: url(../images/common/button-arrow.svg);
  mask-size: contain;
  mask-repeat: no-repeat;
  height: 100%;
  width: 100%;
  position: absolute;
  top: -0.125rem;
  left: 0;
}

.link-button__arrow--white::before {
  background-color: #408F95;
}

.link-button__arrow--404 {
  margin-left: 0.75rem;
}

.link-button__arrow--send {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 3.4375rem;
}

.link-button:hover .link-button__arrow::before {
  background-color: #408F95;
}

.link-button:hover .link-button__arrow--white::before {
  background-color: #fff;
}

.link-button:hover .link-button__text--send {
  color: #408F95;
}

.nav-menu {
  display: flex;
}

.nav-menu__half {
  flex-basis: 50%;
}
.nav-menu__half:last-of-type {
  padding-left: 0.6875rem;
}
@media screen and (min-width: 768px) {
  .nav-menu__half:last-of-type {
    padding-left: 0;
  }
}
@media screen and (min-width: 768px) {
  .nav-menu__half {
    display: flex;
  }
}

.nav-menu__half--sitemap:last-of-type {
  padding-left: 1.375rem;
}
@media screen and (min-width: 768px) {
  .nav-menu__half--sitemap:last-of-type {
    padding-left: 3rem;
  }
}

.nav-menu__quarter:last-of-type {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .nav-menu__quarter:last-of-type {
    margin-top: 0;
  }
}
@media screen and (min-width: 768px) {
  .nav-menu__quarter {
    flex-basis: 50%;
  }
  .nav-menu__quarter:not(.nav-menu__half:first-of-type .nav-menu__quarter:first-of-type) {
    padding-left: 1rem;
  }
}

@media screen and (min-width: 768px) {
  .nav-menu__quarter--sitemap {
    padding-left: 2.5rem !important; /*上記スタイルに負けてしまうため*/
  }
}

@media screen and (min-width: 768px) {
  .nav-menu__half--sitemap .nav-menu__quarter:last-of-type {
    padding-left: 4rem !important; /*上記スタイルに負けてしまうため*/
  }
}

.nav-menu__item {
  line-height: 1.125;
}
.nav-menu__item:not(.sub-menu):not(:first-of-type) {
  margin-top: 1rem;
}
@media screen and (min-width: 768px) {
  .nav-menu__item:first-child {
    margin-top: 0;
  }
}
.nav-menu__item a {
  display: block;
  color: #fff;
  font-weight: 700;
  padding: 0.75rem 0 0.75rem 1.375rem;
  position: relative;
  transition: 0.3s;
}
.nav-menu__item a:hover {
  opacity: 0.8;
  transition: 0.3s;
}

.nav-menu__item--sitemap a {
  color: #0D2936;
}

.nav-menu__item > a::before {
  content: "";
  display: inline-block;
  background-image: url(../images/common/icon-starfish--white.svg);
  width: 1.125rem;
  height: 1.125rem;
  position: absolute;
  left: 0;
  top: 0.75rem;
}

.nav-menu__item--sitemap > a::before {
  background-image: url(../images/common/icon-starfish--black.svg);
}

