.page-header {
  position: relative;
}
.page-header::before {
  content: "";
  display: block;
  background-color: rgba(64, 143, 149, 0.3);
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}

.page-header__img {
  height: 28.75rem;
}
@media screen and (min-width: 768px) {
  .page-header__img {
    height: 34.25rem;
  }
