.page-header__img img {
  height: 100%;
  object-fit: cover;
}

.page-header__title {
  color: #fff;
  font-size: 2.5rem;
  font-weight: 400;
  line-height: 1.25;
  white-space: nowrap;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}
@media screen and (min-width: 768px) {
  .page-header__title {
    font-size: 5rem;
    top: 17.125rem;
    transform: translate(-50%, 0);
  }
}

.page-numbers {
  display: flex;
  justify-content: center;
  align-items: center;
  border: 0.0625rem solid #408F95;
  color: #408F95;
  font-family: "Lato", sans-serif;
  font-size: 1rem;
  font-weight: 700;
  width: 2.375rem;
  height: 2.375rem;
  line-height: 0;
  transition: 0.3s;
}
.page-numbers.prev {
  margin-right: 1.75rem;
  position: relative;
}
.page-numbers.prev::before {
  content: "";
  background-image: url(../images/common/pagenation-arrow--left.svg);
  background-size: contain;
  background-repeat: no-repeat;
  height: 1.0625rem;
  width: 0.5625rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.page-numbers.prev:hover::before {
  background-image: url(../images/common/pagenation-arrow--left-hover.svg);
  transition: 0.3s;
}
.page-numbers.next {
  margin-left: 1.75rem;
  position: relative;
}
.page-numbers.next::before {
  content: "";
  background-image: url(../images/common/pagenation-arrow--right.svg);
  background-size: contain;
  background-repeat: no-repeat;
  height: 1.0625rem;
  width: 0.5625rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.page-numbers.next:hover::before {
  background-image: url(../images/common/pagenation-arrow--right-hover.svg);
  transition: 0.3s;
}
.page-numbers.current {
  background-color: #408F95;
  color: #fff;
}
.page-numbers:hover {
  background-color: #408F95;
  color: #fff;
  transition: 0.3s;
}

